.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}
.header__logo {
  height: 25px;
  object-fit: contain;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__left:hover {
  color: red;
  cursor: pointer;
}
.header__input {
  display: flex;
  align-items: center;
  width: 40%;
  border: 1px solid black;
}
.header__inputButton {
  width: 50px !important;
  background-color: #fafafa;
  border-left: 1px solid lightgray;
  color: gray;
}
.header__input > input {
  flex: 1;
  border: none;
}
.header__icons {
  display: flex;
  align-items: center;
}
.header__icon {
  margin-right: 8px;
}
.header__icon:hover {
  color: red;
  cursor: pointer;
}
